@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.file-upload {
    position: relative;
    display: inline-block;
    margin: 20px;
}

.file-upload__label {
    display: block;
    padding: 1em 2em;
    color: #fff;
    background: #222;
    border-radius: .4em;
    transition: background .3s;
}

.file-upload__label:hover {
     cursor: pointer;
     background: #000;
}

.file-upload__input {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    font-size: 1;
    width:0;
    height: 100%;
    opacity: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.title {
  margin: 20px;
  font-size: 50px;
  font-family: 'Rubik', sans-serif;
}

.wrapper {
  position:relative;
  overflow: hidden;
}

.wrapper .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.elon-musk {
  width: 400px;
  height: 400px;
}

.spinner-box {
    height: 400px;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}

.circle-border {
    width: 75px;
    height: 75px;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: rgb(37, 37, 37);
    background: linear-gradient(0deg, rgba(37, 37, 37, 0.1) 33%, rgb(37, 37, 37) 100%);
    -webkit-animation: spin .8s linear 0s infinite;
            animation: spin .8s linear 0s infinite;
}

.circle-core {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 50%;
}

@-webkit-keyframes spin {
    from {
        transform: rotate(0);
    }
    to{
        transform: rotate(359deg);
    }
}

@keyframes spin {
    from {
        transform: rotate(0);
    }
    to{
        transform: rotate(359deg);
    }
}

