@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.title {
  margin: 20px;
  font-size: 50px;
  font-family: 'Rubik', sans-serif;
}

.wrapper {
  position:relative;
  overflow: hidden;
}

.wrapper .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.elon-musk {
  width: 400px;
  height: 400px;
}
